}

.ba-header__logo-menu {
  height: 80px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 7px;
}

.ba-header__nav {
  display: flex;
  justify-content: flex-end;
  padding-left: 0;
  list-style: none;
}

.ba-header__item {
  display: flex;
  align-items: center;
  margin-left: 9px;
  margin-right: 9px;
  min-height: 80px;
}

.ba-header__link {
  height: 33px;
  padding: 10px 15px;
  border-radius: 5px;
  color: #fff;
  font-weight: 600;
  text-transform: uppercase;
  text-decoration: none;
}

.ba-header__link--active {
  background-color: #62a29e;
  border-bottom: 3px solid #528b86;
  font-size: 14px;
}

.ba-header__link:hover {
  background-color: #e8645a;
  border-bottom: 3px solid #e2534b;
}

.ba-header__link-dropdown {
  position: relative;
}

.ba-header__link-dropdown:before {
  content: url("../img/arrow-down.png");
  margin-right: 8px;
  position: relative;
  bottom: 10px;
  visibility: hidden;
}

.ba-header__search {
  order: 2;
  min-height: 80px;
  vertical-align: middle;
  line-height: 80px;
  margin-left: 26px;
  position: relative;
  flex-shrink: 0;
}

.ba-header__search-img {
  position: relative;
  top: 4px;
}

.ba-header__search-input {
  position: absolute;
  right: 0;
  transform: translateY(-100%);
  color: #777777;
}

.ba-dropmenu {
  display: none;
  position: absolute;
  background-color: #e2534b;
  border-radius: 5px;
  padding: 5px 5px 0;
  width: 200px;
  top: 70px;
}

.ba-dropmenu__triangle {
  position: absolute;
  transform: translateY(-100%);
  border: 14px solid transparent;
  border-bottom: 12px solid #e2534b;
  width: 0;
  height: 0;
  z-index: 0;
}

.ba-dropmenu__list {
  position: relative;
  z-index: 2;
}

.ba-dropmenu__item {
  width: 100%;
  border-radius: 2px 3px 3px 2px;
  background-color: rgba(255, 255, 255, 0.1);
  margin-bottom: 5px;
  padding: 10px;
  display: flex;
  align-items: center;
}

.ba-dropmenu__item:hover {
  background-color: #76c7c0;
}

.ba-dropmenu__link {
  color: #fff;
  font-weight: 600;
}

.ba-dropmenu__link:before {
  content: url("../img/arrow-right.png");
  margin-right: 9px;
}

.ba-header__item-dropdown:hover > .ba-dropmenu {
  display: block;
}

.ba-header__item-dropdown:hover > .ba-header__link-dropdown {
  background-color: #e8645a;
  border-bottom: 3px solid #e2534b;
}

.ba-header__item-dropdown:hover > .ba-header__link-dropdown:before {
  visibility: visible;
  bottom: 2.5px;
  transition: all 1s;
}

.ba-header__search > input {
  display: none;
}

.ba-header__search--active > input {
  display: block;
}

#hamburger,
.ba-header__search-img,
.ba-header__item,
.ba-dropmenu__item {
  cursor: pointer;
}

#hamburger {
  width: 20px;
  height: 15px;
  position: relative;
  align-self: center;
  -webkit-transform: rotate(0deg);
  -moz-transform: rotate(0deg);
  -o-transform: rotate(0deg);
  transform: rotate(0deg);
  -webkit-transition: 0.5s ease-in-out;
  -moz-transition: 0.5s ease-in-out;
  -o-transition: 0.5s ease-in-out;
  transition: 0.5s ease-in-out;
  cursor: pointer;
}

#hamburger span {
  display: block;
  position: absolute;
  height: 1px;
  width: 20px;
  background: #fff;
  opacity: 1;
  -webkit-transform: rotate(0deg);
  -moz-transform: rotate(0deg);
  -o-transform: rotate(0deg);
  transform: rotate(0deg);
  -webkit-transition: .25s ease-in-out;
  -moz-transition: .25s ease-in-out;
  -o-transition: .25s ease-in-out;
  transition: .25s ease-in-out;
}

#hamburger span:nth-child(1) {
  top: 3px;
}

#hamburger span:nth-child(2), #hamburger span:nth-child(3) {
  top: 8px;
}

#hamburger span:nth-child(4) {
  top: 13px;
}

#hamburger.ba-hamburger--open span:nth-child(1) {
  top: 8px;
  width: 0;
  left: 50%;
}

#hamburger.ba-hamburger--open span:nth-child(2) {
  -webkit-transform: rotate(45deg);
  -moz-transform: rotate(45deg);
  -o-transform: rotate(45deg);
  transform: rotate(45deg);
}

#hamburger.ba-hamburger--open span:nth-child(3) {
  -webkit-transform: rotate(-45deg);
  -moz-transform: rotate(-45deg);
  -o-transform: rotate(-45deg);
  transform: rotate(-45deg);
}

#hamburger.ba-hamburger--open span:nth-child(4) {
  top: 8px;
  width: 0;
  left: 50%;
}

