  text-align: left;
}

.ba-footer-first__items {
  margin: 0 15px 20px;
}

.ba-footer-first__about {
  max-width: 370px;
  order: 1;
}

.ba-footer-first__info {
  margin: 28px 0 23px;
  line-height: 23px;
}

.ba-footer-first__phone, .ba-footer-first__mail {
  margin-bottom: 10px;
}

.ba-footer-first__phone span, .ba-footer-first__mail span {
  font-size: 20px;
  font-weight: 600;
  font-style: italic;
  margin-left: 20px;
}

.ba-footer-first__links {
  max-width: 200px;
  order: 2;
}

.ba-footer-first__links h3 {
  color: #3b6360;
  font-size: 20px;
  font-weight: 700;
  margin-bottom: 30px;
}

.ba-footer-first__links ul {
  padding-left: 23px;
}

.ba-footer-first__link {
  line-height: 23px;
  color: #fff;
  text-decoration: none;
}

.ba-footer-first__link:before {
  content: url("../img/list-item-white.png");
  margin-right: 9px;
}

.ba-footer-first__blog {
  max-width: 370px;
  order: 3;
}

.ba-footer-first__blog h3 {
  font-size: 20px;
  font-style: italic;
  margin-bottom: 24px;
}

.ba-footer-first__blog h3 span {
  font-weight: 700;
  font-style: normal;
}

.ba-footer-first__blog-message {
  display: flex;
  margin-bottom: 20px;
}

.ba-footer-first__blog-img {
  width: 70px;
  height: 70px;
  border-radius: 5px;
  background-color: #5e9f9a;
  color: rgba(255, 255, 255, 0.5);
  font-size: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-shrink: 0;
}

.ba-footer-first__blog-info {
  margin-left: 18px;
  line-height: 20px;
}

.ba-footer-first__blog-data {
  color: rgba(0, 0, 0, 0.5);
}

.ba-footer__section-second {
  background-color: #6ab3ac;
}

.ba-footer-second__container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  padding-left: 7px;
}

.ba-footer-second__copyright {
  font-size: 12px;
  margin: 14px 0;
}

.ba-footer-second__social-links {
  margin: 14px 0;
}

.ba-footer-second__social-links a {
  margin-left: 26px;
}

@media (min-width: 985px) {
  .ba-header__menu {
    display: flex;
  }
  .ba-header__menu #hamburger {
    display: none;
  }
}

@media (max-width: 985px) {
  .ba-header__menu {
    display: block;
  }
  #hamburger {
    display: inline-block;
  }
  .ba-header__search {
    display: inline-block;
  }
  .ba-header__search-img {
    top: 0;
    padding-right: 10px;
  }
  .ba-header__nav {
    display: none;
    position: absolute;
    background-color: #76c7c0;
    border-top: 1px solid #62a29e;
    padding-top: 15px;
    right: 0;
  }
  .ba-header__nav .ba-header__item {
    text-align: right;
    display: block;
    min-height: 42px;
  }
  .ba-header__menu.ba-header__menu--open .ba-header__nav {
    display: block;
  }
  .ba-dropmenu {
    right: 100%;
    top: 95px;
    text-align: left;
  }
  .ba-dropmenu__triangle {
    border: 14px solid transparent;
    border-left: 12px solid #e2534b;
    top: 59%;
    left: 100%;
    z-index: 0;
  }
}

@media (max-width: 360px) {
  .ba-dropmenu {
    width: 150px;
    top: 73px;
  }
}

@media (max-width: 640px) {
  .slick-prev,
  .slick-next {
    visibility: hidden;
  }
  .ba-home__title {
    font-size: 2rem;
  }
}

@media (max-width: 780px) {
  .ba-about .slick-current {
    transform: scale(1, 1);
  }
  .ba-about .slick-current .ba-about__slider-header {
    transform: scale(1, 1);
    width: 100%;
    margin-left: 0;
  }
}

@media (max-width: 1081px) {
  .ba-about__skills {
    order: 1;
  }
  .ba-about__description {
    justify-content: center;
  }
}

@media (max-width: 714px) {
  .ba-footer-first__container {
    justify-content: center;
  }
  .ba-footer-first__about {
    max-width: 100%;
    text-align: center;
  }
}

@media (max-width: 350px) {
  .ba-footer-second__container {
    justify-content: center;
  }
}

body {
  background-color: #f3f3f3;
  font-family: "Open Sans", sans-serif;
  color: #fff;
  font-size: 13px;
  text-align: center;
}

.ba-container {
  max-width: 1190px;
  padding: 0 10px;
}

.slick-slide {
  outline: none;
}
